<template>
  <b-container fluid>
    <b-row>
      <b-col>
        <p>Viewing last {{windowSize}} feature vectors</p>
        <div class="d-flex justify-content-center">
          <label for="range-2">Adjust the feature stream window size with this slider: </label>
          <b-form-input class="stream-window-slider ml-2" id="range-2" v-model="windowSize" type="range" min=1 max=10 step=1></b-form-input>
        </div>
        <p class="text-left w-50 para">The Dota 2 connector throttles front end feature stream updates such that 1 feature vector is sent every 1500ms. This ensures this page remains responsive while getting near real time updates.
          Actual dota 2 client latency is displayed in the server status, and is unrelated to the 1500ms delay in updating the table below. This means you'll likely continue to see updates here after your run 
          has terminated, they will continue until all feature vectors from the run are streamed to this page.
          A wider window displays additional data but may result in degraded performance in the browser. 
          This does not affect the game, or agent vector-action code transmission just the responsiveness of this page in your browser.</p>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <div class="track-container w-100 d-flex">
          <b-container fluid class="legend track-element flex-fill">
            <b-row class="time text-uppercase">
              <b-col>
                {{featureMap[56] !== undefined? featureMap[56].name: ""}}
              </b-col>
            </b-row>
            <b-row v-for="(field,i) in featureMap"
              :key="i"
              class="text-values"
            >
              <b-col>
                {{field.name}}
              </b-col>
            </b-row>
          </b-container>
          <b-container fluid v-for="(vector,i) in featureVectors"
            :key="i"
            class="track-element flex-fill">
            <b-row class="time">
              <b-col>
                {{vector[56]}}
              </b-col>
            </b-row>
            <b-row v-for="(element,j) in vector"
              :key="j"
              class="text-values"
            >
              <b-col class="vector-item">
                <span class="value">{{element}}</span>
                <span class="delta">
                <v-icon v-if="featureVectors[i-1] !== undefined && element-featureVectors[i-1][j] > 0" name="arrow-up" style="color:green"></v-icon>
                <v-icon v-if="featureVectors[i-1] !== undefined && element-featureVectors[i-1][j] < 0" name="arrow-down" style="color:red"></v-icon>
                {{featureVectors[i-1]&&element-featureVectors[i-1][j] !== 0?element-featureVectors[i-1][j]:""}}
                </span>
              </b-col>
            </b-row>
          </b-container>
        </div>
      </b-col>
    </b-row>
    
  </b-container>
</template>

<script>
import EventBus from 'vertx3-eventbus-client'
import SockJS from 'sockjs-client'
import AbstractXHRObject from 'sockjs-client/lib/transport/browser/abstract-xhr';
import 'sockjs-client'
import axios from 'axios';
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/arrow-up'
import 'vue-awesome/icons/arrow-down'

const _start = AbstractXHRObject.prototype._start;

AbstractXHRObject.prototype._start = function(method, url, payload, opts) {
    if (!opts) {
        opts = { noCredentials : true };
    }
    return _start.call(this, method, url, payload, opts);
};


export default {
  name: 'VectorTrack',
  components: {'v-icon': Icon},
  data(){
    return{
      eb: {},
      featureVectors:[],
      featureMap: [],
      timeouts:[],
      lastUpdate: undefined,
      windowSize: 5
    }
  },
  beforeCreate(){
    var instance = this;

    axios.get('http://localhost:8085/features/')
    .then(function(response){
      instance.featureMap = response.data
    })

    var sock = new SockJS('http://localhost:8085/stream/features')

    sock.onopen = function(){
      console.log('socket open')
    }

    sock.onmessage = function(msg){
      if(instance.lastUpdate !== undefined){
        var newTime = new Date().getTime()
        var delta =  newTime - instance.lastUpdate
        console.log(delta + "ms")
        instance.lastUpdate = newTime
      }else{
        instance.lastUpdate = new Date().getTime()
      }
      

      console.log(msg.data)
      var vector = JSON.parse(msg.data)
      instance.featureVectors.push(vector)

      while(instance.featureVectors.length > instance.windowSize){
        instance.featureVectors.shift()
      }

    }

    sock.onclose = function(){
      console.log('socket closed')
    }

  },
  methods:{
    hoverItem: function(evt){
        console.log('woah')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.track-container{
  overflow-x: auto;
}

.track-element{
  border-left: solid 2px #ababab;
}

.time{
  color: #B47f22;
  font-size: 16pt;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  border-bottom: 4px solid #B47f22;
  border-left: none !important;
}

.vector-item:hover{
  background-color:#B47f22;
}

.delta{
  float: right;
}

.value{
  position: relative;
  float:left;
}

.stream-window-slider{
  max-width: 200px;
}

.legend{
  max-width: 400px;
}

.text-values{
  color: #f2f2f2;
  font-size:16pt;
}

.para{
  margin-right: auto;
  margin-left: auto;
}
</style>
