<template>
    <b-container fluid>
        <div class="d-flex justify-content-between">
            <div class="d-flex flex-column justify-content-between">
                <div class="d-flex flex-column rune-panel">
                    <h3 class="panel-title text-uppercase text-left">Get Runes</h3>
                    <div class="d-flex flex-wrap">
                        <div class="action-button-wide m-2"
                            v-for="(action,i) in runes"
                            :key="i"
                            @click="setAction(action.code)"
                            v-b-tooltip.hover :title="action.name"
                        >
                            <span class="button-text-rune" v-if="action.position == 0">{{action.name.substring(4,action.name.length)}}</span>
                            <span class="button-text-rune" v-if="action.position == 1">{{action.name.substring(4,action.name.length)}}</span>
                            <span class="button-text-rune" v-if="action.position == 2">{{action.name.substring(4,action.name.length)}}</span>
                            <span class="button-text-rune" v-if="action.position == 3">{{action.name.substring(4,action.name.length)}}</span> 
                        </div>
                    </div>
                </div>

                <div class="d-flex flex-column cast-panel">
                    <h3 class="panel-title text-uppercase text-left">Cast</h3>
                    <div class="d-flex flex-wrap">
                        <div class="action-button-cast m-2"
                            v-for="(action,i) in cast"
                            :key=i
                            @click="setAction(action.code)"
                            >

                            <b-img v-b-tooltip.hover title="Cast Shadow Raze 1 (Near)" class="button-img" v-if="action.position == 0" src="../assets/shadowraze_1.png"></b-img>
                            <b-img v-b-tooltip.hover title="Cast Shadow Raze 2 (Medium)" class="button-img" v-if="action.position == 1" src="../assets/shadowraze_2.png"></b-img>
                            <b-img v-b-tooltip.hover title="Cast Shadow Raze 1 (Far)" class="button-img" v-if="action.position == 2" src="../assets/shadowraze_3.png"></b-img>
                            <b-img v-b-tooltip.hover title="Cast Requiem of Souls" class="button-img" v-if="action.position == 3" src="../assets/ros.png"></b-img>
                            <b-img v-b-tooltip.hover title="Use Salve" class="button-img" v-if="action.position == 4" src="../assets/salve.png"></b-img>
                        </div>
                    </div>
                </div>                
            </div>

        <div class="d-flex flex-column attack-creep-panel">
            <h3 class="panel-title text-uppercase text-left">Attack Creep</h3>
            <div class="d-flex flex-wrap">
                <div class="action-button m-2"
                    v-for="(action,i) in attackCreep"
                    :key=i
                    @click="setAction(action.code)"
                    v-b-tooltip.hover :title="action.name"
                >
                    <span class="button-number" v-if="action.position < 10">{{action.position}}</span>
                    <div class="button-text" v-if="action.position == 10">{{action.name}}</div>
                    <span class="button-number" v-if="action.position == 11">0</span>
                    <div class="button-text" v-if="action.position == 12">{{action.name}}</div>
                </div>

            </div>
        </div>


        <div class="d-flex flex-column move-panel">
            <h3 class="panel-title text-uppercase text-left">Move</h3>
            <div class="d-flex flex-wrap">
                <div class="action-button m-2 " 
                    v-for="(action,i) in move"
                    :key=i
                    @click="setAction(action.code)"
                    v-b-tooltip.hover :title="action.name"
                >
                    <v-icon v-if="action.action=='MOVE_N'" class="button-icon north" name="arrow-up" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_NE'" class="button-icon" name="arrow-right" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_NW'" class="button-icon" name="arrow-up" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_W'" class="button-icon west" name="arrow-up" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_E'" class="button-icon east" name="arrow-up" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_S'" class="button-icon south" name="arrow-up" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_SE'" class="button-icon" name="arrow-down" scale="3"></v-icon>
                    <v-icon v-if="action.action=='MOVE_SW'" class="button-icon" name="arrow-left" scale="3"></v-icon>
                    <v-icon v-if="action.action=='DO_NOTHING'" class="button-icon" name="ban" scale="3"></v-icon>
            
                </div>
            </div>
        </div>
        </div>

        

        
    </b-container>
</template>
<script>
import 'vue-awesome/icons/ban'
import 'vue-awesome/icons/arrow-up'
import 'vue-awesome/icons/arrow-down'
import 'vue-awesome/icons/arrow-left'
import 'vue-awesome/icons/arrow-right'
import Icon from 'vue-awesome/components/Icon'
import axios from 'axios'
export default {
    name: "ActionsBar",
    components: {'v-icon':Icon},
    data(){
        return {
            actions:[],
        }
    },
    mounted(){

        var instance = this
        axios.get('http://localhost:8085/action/')
            .then(function(response){
                instance.actions = response.data
            })
            .catch(function(response){
                console.log('Error fetching actions from server!')
            })
    },
    computed: {
        runes: function(){
            let result = []
            for(let i = 0; i < this.actions.length; i++){
                let curr = this.actions[i]
                if(curr.action.includes("RUNE")){
                    switch(curr.action){
                        case "GET_BOUNTY_RUNE_1":
                            curr.position = 0
                            break
                        case "GET_TOP_POWERUP_RUNE":
                            curr.position = 1
                            break
                        case "GET_BOUNTY_RUNE_3":
                            curr.position = 2
                            break
                        case "GET_BOTTOM_POWERUP_RUNE":
                            curr.position = 3
                            break
                    }
                    result.push(curr)

                    
                }
            }

            result.sort(function(a,b){
                return a.position-b.position
            })

            return result
        },
        attackCreep: function(){
            let result = []
            for(let i = 0; i < this.actions.length; i++){
                let curr = this.actions[i]
                if(curr.action.includes("ATTACK_CREEP") ||
                    curr.action.includes("ATTACK_ENEMY")
                ){
                    switch(curr.action){
                        case "ATTACK_CREEP_1":
                            curr.position = 1
                            break
                        case "ATTACK_CREEP_2":
                            curr.position = 2
                            break
                        case "ATTACK_CREEP_3":
                            curr.position = 3
                            break
                        case "ATTACK_CREEP_4":
                            curr.position = 4
                            break
                        case "ATTACK_CREEP_5":
                            curr.position = 5
                            break
                        case "ATTACK_CREEP_6":
                            curr.position = 6
                            break
                        case "ATTACK_CREEP_7":
                            curr.position = 7
                            break
                        case "ATTACK_CREEP_8":
                            curr.position = 8
                            break
                        case "ATTACK_CREEP_9":
                            curr.position = 9
                            break
                        case "ATTACK_ENEMY_HERO":
                            curr.position = 10
                            break
                        case "ATTACK_CREEP_0":
                            curr.position = 11
                            break
                        case "ATTACK_ENEMY_TOWER":
                            curr.position = 12
                            break

                    }
                    result.push(curr)
                }
            }
            result.sort(function(a,b){
                return a.position-b.position
            })
            return result
        },
        cast: function(){
            let result = []
            for(let i = 0; i < this.actions.length; i++){
                let curr = this.actions[i]
                if(curr.action.includes("CAST") || curr.action.includes("USE_SALVE")){
                    switch(curr.action){
                        case "CAST_SHADOWRAZE_1":
                            curr.position = 0
                            break
                        case "CAST_SHADOWRAZE_2":
                            curr.position = 1
                            break
                        case "CAST_SHADOWRAZE_3":
                            curr.position = 2
                            break
                        case "CAST_REQUIEM_OF_SOULS":
                            curr.position = 3
                            break
                        case "USE_SALVE":
                            curr.position = 4
                            break
                    }

                    result.push(curr)
                }
            }
            result.sort(function(a,b){
                return a.position - b.position
            })

            return result
        },
        move: function(){
            let result = []
            for(let i = 0; i < this.actions.length; i++){
                let curr = this.actions[i]
                if(curr.action.includes("MOVE") || curr.action.includes("DO_NOTHING")){
                    switch (curr.action){
                        case "MOVE_NW":
                            curr.position = 1
                            break
                        case "MOVE_N":
                            curr.position = 2
                            break
                        case "MOVE_NE":
                            curr.position = 5
                            break
                        case "MOVE_W":
                            curr.position = 0
                            break
                        case "DO_NOTHING":
                            curr.position = 4
                            break
                        case "MOVE_E":
                            curr.position = 8
                            break
                        case "MOVE_SW":
                            curr.position = 3
                            break
                        case "MOVE_S":
                            curr.position = 6
                            break
                        case "MOVE_SE":
                            curr.position = 7
                            break
                    }

                    result.push(curr)
                }
            }
            result.sort(function(a, b){
                return a.position-b.position                
            })

            return result
        }
    },
    methods: {
        setAction: function(value){
            var instance = this
            axios.get('http://localhost:8085/action?value=' + value)
            .then(function(response){
                instance.$emit('server-state-update', response.data)
            })
        }
    }
}
</script>
<style scoped>
.action-item{
    background-color: #a3a3a3;
}

.action-item:hover{
    background-color: #737373;
    cursor: pointer;
}

.action-button{
    width: 75px;
    height: 75px;
    background-color: #6c6161;
    border: 2px solid #B47F22;
    cursor: pointer;
}

.action-button-cast{
    width: 75px;
    height: 75px;
    background-color: #6c6161;
    border: 2px solid #B47F22;
    cursor: pointer;
}

.action-button-wide{
    width: 125px;
    height: 75px;
    background-color: #6c6161;
    border: 2px solid #B47F22;
    cursor: pointer;
}


.action-button-wide:hover{
    color:#B47F22;
}

.action-button:hover{
    color:#B47F22
}

.action-button-cast:hover{
    border: 4px solid #B47F22;
    cursor: pointer;
}

.button-icon{
    position: relative;
    top: 25%;
}

.button-number{
    position: relative;
    top: 25%;
    font-family: 'Calisto MT', 'serif';
    font-size: 22pt; 
}


.button-text{
    position: relative;
    font-family: 'Calisto MT', 'serif';
    font-size: 12pt;
    line-height: 1.5em;
}

.button-text-rune{
    position: relative;
    font-family: 'Calisto MT', 'serif';
    font-size: 12pt;
    line-height: 1.25em;
    top: 20%;
}

.button-img{
    width: 71px;
    height: 71px;
}

.north{
    transform: rotate(45deg)
}

.west{
    transform: rotate(315deg)
}

.east{
    transform: rotate(135deg)
}

.south{
    transform: rotate(225deg)
}

.panel-title{
    color:#B47F22;
    font-family: 'Open Sans', sans-serif;
    font-size: 16pt;
    font-weight: 600;
}

.move-panel{
    width:300px;
}

.attack-creep-panel{
    width: 300px;
}

.rune-panel{
    width:300px;
}

</style>