<template>
    <b-container fluid>
        <b-alert variant="warning" :show="activeRunAlreadyExistsAlert" dismissible>Cannot start run, there's already an active run!</b-alert>
        <b-alert variant="warning" :show="noActiveRunToCancelAlert" dismissible>Cannot cancel run, there's no active run to cancel!</b-alert>
        <b-row>
            <b-col cols="3">
                <div class="d-flex flex-column">
                    <div class="d-flex">
                        <div class="action-button action-start" @click="startRun">
                            <p class="button-text">Start Run</p>
                        </div>
                        <div class="d-flex flex-column">
                            <label for="run-size"># of games:</label>
                        <b-form-input id="run-size" class="ml-2 start-run-input" type="number" min="1" v-model="startRunSize"></b-form-input>
                        </div>
                    </div>
                    <div class="d-flex mt-2">
                        <div class="action-button action-cancel" @click="cancelRun">
                            <p class="button-text">Cancel Run</p>
                        </div>
                    </div>
                    <div class="d-flex mt-2">
                        <div class="action-button action-restart" @click="restartRun">
                            <p class="button-text">Restart Run</p>
                        </div>
                    </div>
                </div>
            </b-col>
            <b-col cols="9">
                <div v-if="activeRun !== undefined" class="d-flex flex-column">
                    <h2 class="text-left panel-title">Active Run</h2>
                    <b-progress :max="activeRun.size" show-progress animated>
                        <b-progress-bar :value="activeRun.progress" :label="`${((activeRun.progress / activeRun.size) * 100).toFixed(0)}%`"></b-progress-bar>
                    </b-progress>
                    <div v-if="activeRun.error !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Pseudo unique id of the current game" class="text-left run-property">error:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.error}}</p>
                    </div>
                    <div v-if="activeRun.activeGameId !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Pseudo unique id of the current game" class="text-left run-property">activeGameId:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.activeGameId}}</p>
                    </div>
                    <div v-if="activeRun.duration !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Time since the start of the run in milliseconds" class="text-left run-property">duration:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.duration}} ms</p>
                    </div>
                    <div v-if="activeRun.gameIds !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Ids of completed games in this run" class="text-left run-property">gameIds:</h3>
                        <p class="text-left run-property-value ml-2" v-for="(gid,i) in activeRun.gameIds"
                         :key=i>{{activeRun.gameIds[i]}}</p>
                    </div>
                    <div v-if="activeRun.crashedGames !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Ids of crashed games in this run" class="text-left run-property">crashedGames:</h3>
                        <p class="text-left run-property-value ml-2" v-for="(gid,i) in activeRun.crashedGames"
                         :key=i>{{activeRun.crashedGames[i]}}</p>
                    </div>
                    <div v-if="activeRun.id !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="UUID of this run" class="text-left run-property">id:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.id}}</p>
                    </div>
                    <div v-if="activeRun.progress !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Number of completed games so far" class="text-left run-property">progress:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.progress}}</p>
                    </div>
                    <div v-if="activeRun.size !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Total number of games to play in this run" class="text-left run-property">size:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.size}}</p>
                    </div>
                    <div v-if="activeRun.startTime !== undefined" class="d-flex">
                        <h3  class="text-left run-property">startTime:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.startTime}}</p>
                    </div>
                    <div v-if="activeRun.status !== undefined" class="d-flex">
                        <h3 class="text-left run-property">status:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.status}}</p>
                    </div>
                    <div v-if="activeRun.endTime !== undefined" class="d-flex">
                        <h3 class="text-left run-property">endTime:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.endTime}}</p>
                    </div>
                    <div v-if="activeRun.webhook !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="Webhook URI that must be hit by a Breezy Agent to proceed to the next game in the run" class="text-left run-property">webhook:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.webhook}}</p>
                    </div>
                    <div v-if="activeRun.winner !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="The team that one the last game" class="text-left run-property">winner:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.winner}}</p>
                    </div>
                    <div v-if="activeRun.direKills !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="The number of kills the Dire team achieved last game" class="text-left run-property">direKills:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.direKills}}</p>
                    </div>
                    <div v-if="activeRun.radiantKills !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="The number of kills the Radiant team achieved last game" class="text-left run-property">radiantKills:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.radiantKills}}</p>
                    </div>
                    <div v-if="activeRun.deaths !== undefined" class="d-flex">
                        <h3 v-b-tooltip.hover title="The number of deaths the AI controlled bot suffered last game" class="text-left run-property">deaths:</h3>
                        <p class="text-left run-property-value ml-2">{{activeRun.deaths}}</p>
                    </div>
                </div>
            </b-col>
        </b-row>
    </b-container>
</template>
<script>
import axios from 'axios'
import EventBus from 'vertx3-eventbus-client'
import 'sockjs-client'
export default {
    name: 'RunControlPanel',
    data(){
        return{
            activeRun: undefined,
            startRunSize: 1,
            activeRunAlreadyExistsAlert: false,
            noActiveRunToCancelAlert: false
        }
    },
    beforeCreate(){
        let instance = this
        let eb = new EventBus('http://localhost:8085/eventbus/')

        eb.onopen = function(){
            eb.registerHandler('sentry.activeRun', function(a,message){
                instance.activeRun = message.body
                console.log('active run data updated!')
            })
        }
    },
    mounted: function(){
        let instance = this

        axios.get('http://localhost:8085/run/active')
        .then(function(response){

            console.log(response)
            instance.activeRun = response.data

        }).catch(function(error){

            if(error.response.status === 404){
                instance.activeRun = error.response.data
            }

        })
    },
    methods: {
        getActiveRun(callback){
            let instance = this

            axios.get('http://localhost:8085/run/active')
            .then(function(response){
                instance.activeRun = response.data
                if(callback) callback();
            }).catch(function(error){
                if(error.response.status === 404){
                    instance.activeRun = error.response.data
                    if(callback) callback();
                }
            })
        },
        startRun(callback){
            let instance = this

            //Reset alerts
            instance.activeRunAlreadyExistsAlert = false
            instance.noActiveRunToCancelAlert = false

            let run = {
                size: parseInt(instance.startRunSize),
                agent: 'Sentry'
            }

            axios.post('http://localhost:8085/run/', run)
            .then(function(response){
                instance.activeRun = response.data

                if(callback) callback();

            }).catch(function(error){
                
                if(error.response !== undefined && error.response.data.error != undefined && error.response.data.error.includes("there already is an active run!")){
                    instance.activeRunAlreadyExistsAlert = true
                }

                
            })
        },
        cancelRun(callback){
            let instance = this

            //Reset alerts
            instance.activeRunAlreadyExistsAlert = false
            instance.noActiveRunToCancelAlert = false

            axios.delete('http://localhost:8085/run/active')
            .then(function(response){
                instance.activeRun = response.data
                if(callback) callback();
            })
            .catch(function(error){
                console.log(error)
                console.log(error.response)
                if(error.response !== undefined &&  error.response.data.error != undefined && error.response.data.error.includes("No active run!")){
                    instance.noActiveRunToCancelAlert = true
                }
            })
        },
        restartRun(){
            let instance = this
            instance.getActiveRun(function(){

                if (instance.activeRun.error !== undefined){
                    instance.startRun()    
                }else{
                    console.log("run already exists, cancelling then starting")
                    instance.cancelRun(function(){
                        setTimeout(instance.startRun(), 500)
                    })
                }

            })
            

        }
    }
}
</script>
<style scoped>

input[type=number]{
    background-color: #6c6161 !important;
    color: #ffffff
}

.start-run-input{
    width: 100px;
}

.action-button{
    width: 125px;
    border: 2px solid #B47F22;
    cursor: pointer;
}

.action-start{
    background-color: #336b52;
}

.action-cancel{
    background-color: #6b3333;
}

.action-restart{
    background-color: #6b6733;
}

.button-text{
    position: relative;
    font-family: 'Calisto MT', 'serif';
    font-size: 12pt;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px;
}

.panel-title{
    color:#B47F22;
    font-family: 'Open Sans', sans-serif;
    font-size: 16pt;
    font-weight: 600;
    text-transform: uppercase
}

.run-property{
    color:#B47F22;
    font-family: 'Open Sans', sans-serif;
    font-size: 13pt;
    font-weight: 600;
    margin: 0;
    padding: 0;
}

.run-property-value{
    color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    font-size: 11pt;
    font-weight: 600;
    margin: 0;
    padding: 0;
}

</style>