<template>
  <div id="app">
    <b-container class="background h-100" fluid>
      <b-row class="m-5 server-state-general">
          <b-col>
            <div class="d-flex">
              <img class="dota-logo" src="./assets/logo.png">
              <b-container fluid>
                <b-row>
                  <b-col class="d-flex">
                    <h2 class="server-state-title">Server State</h2>
                  </b-col>
                </b-row>
                <b-row class="server-state-details">
                  <b-col class="d-flex justify-content-between">
                    <div class="d-flex flex-column justifiy-content-start text-left">
                      <h4 class="text-capitalize">Override Agent Action Codes? {{serverState.overrideAgent}}</h4>
                      <h4>Transmitting Action Code: {{serverState.actionCode}}</h4>
                      <h4>Average Client Latency: {{serverState.averageClientLatency}}ms</h4>
                    </div>
                    <div class="ml-5 latency-box">
                      <h4 v-if="!editDotaBetaFolder" @click="editDotaBetaFolderPath()" v-b-tooltip.hover title="Click to edit" class="text-left">Dota Beta Folder Path: {{serverState.dotaBetaFolderPath}}<v-icon class="ml-4" name="pencil-alt"></v-icon></h4>
                      <div v-else class="d-flex justify-content-between">
                        <b-alert :show="showPathValidationError" dismissible variant="danger" class="alert">No spaces are allowed in the dota beta folder path. On windows the command 'dir /x' will list a space free mnemonic for any folder. Ex: "Program Files (x86)" becomes "PROGRA~2", "dota 2 beta" becomes "DOTA2B~1"</b-alert>
                        <b-form-input v-model="dotaBetaFolderPath"></b-form-input>
                        <span @click="editDotaBetaFolderPath()"><v-icon name="times" class="ml-4"></v-icon></span>
                        <span @click="saveDotaBetaFolderPath()"><v-icon class="ml-4" name="check"></v-icon></span>
                      </div>
                      <h4 class="text-left">Latency Records:</h4>
                      <div class="d-inline-flex flex-wrap">
                        <div v-for="(point,i) in serverState.clientLatencyData"
                          :key="i"
                          class="latency-record">
                          {{point}} 
                          </div>
                      </div>
                    </div>
                    <div class="text-bottom">
                        <p class="text-right text-bottom">Design by <a href="http://arinmac.ca">arinmac.ca</a></p>
                      </div>
                  </b-col>
                </b-row>
              </b-container>
            </div>
          </b-col>
      </b-row>
      <b-row class="m-5">
        <b-col cols=8>
          <h2 class="text-left title">Actions</h2>
          <div class="outline">
            <actions-bar @server-state-update="updateServerState"></actions-bar>
          </div>

          <h2 class="text-left title mt-2">Run Control Panel</h2>
          <div class="outline">
            <run-control-panel></run-control-panel>
          </div>
        </b-col>
        <b-col cols=4>
          <h2 class="text-left title">Agent Configuration</h2>
          <div class="outline">
            <AgentConfig @serverState="updateServerState"></AgentConfig>
          </div>
        </b-col>
      </b-row>
      <b-row class="m-5">
        <b-col cols=12>
          <h2 class="text-left title">Features Table</h2>
          <div class="outline">
            <VectorTrack/>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/pencil-alt'
import 'vue-awesome/icons/times'
import 'vue-awesome/icons/check'
import EventBus from 'vertx3-eventbus-client'
import 'sockjs-client'
import ActionsBar from './components/ActionsBar'
import AgentConfig from './components/AgentConfig'
import VectorTrack from './components/VectorTrack'
import RunControlPanel from './components/RunControlPanel'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import axios from 'axios'
export default {
  name: 'app',
  components: {
    VectorTrack,
    'actions-bar': ActionsBar,
    AgentConfig,
    'v-icon' : Icon,
    'run-control-panel': RunControlPanel
  },
  data(){
    return{
      serverState: {
        actionCode: -1
      },
      editDotaBetaFolder: false,
      dotaBetaFolderPath: '',
      showPathValidationError: false
    }
  },
  beforeCreate(){
    var instance = this;
    var eb = new EventBus('http://localhost:8085/eventbus/')

    eb.onopen = function(){
      console.log(eb)
      eb.registerHandler('sentry.latency', function(a,message){
        instance.serverState = message.body
        console.log('Latency updated')
      })
    }
  },
  mounted(){
    var instance = this
    axios.get('http://localhost:8085/state/').then(function(response){
      instance.serverState = response.data
    })
  },
  methods:{
    updateServerState: function(data){
      this.serverState = data
    },
    editDotaBetaFolderPath(){
      console.log('woah')
      this.showPathValidationError = false
      this.editDotaBetaFolder = !this.editDotaBetaFolder
      this.dotaBetaFolderPath = this.serverState.dotaBetaFolderPath
    },
    saveDotaBetaFolderPath(){
      let instance = this
      instance.showPathValidationError = false
      if(this.dotaBetaFolderPath.includes(' ')){
        this.showPathValidationError = true
      }else{
        axios.post('http://localhost:8085/state', {
          dotaBetaFolderPath: instance.dotaBetaFolderPath
        }).then(function(response){
          instance.serverState = response.data
          instance.editDotaBetaFolder = false
        }).catch(function(error){
          console.log('Error updating server state')
        })
      }
    }
  }
}
</script>

<style>

.alert{
  position: fixed;
  width: fit-content;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.latency-record{
  padding: 5px;
}

body{
  min-height: 100%;
  background-color:#3b3535;
}

#app {
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #f2f2f2;
}

.dota-logo{
  height: 200px;
  width: 200px;
}

.outline{
  border: 4px solid #6C6161;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

.title{
  color: #6C6161;
  font-size: 18pt;
  font-weight: 600;
}

.server-state-general{
  color: #6C6161;
}

.server-state-title{
  font-size: 27pt;
  font-weight: 600;
}

.server-state-details h4{
  font-size: 16pt !important;
}

.latency-box{
  width: 1200px;
}

a{
  color: #6C6161;
  text-decoration:none;
}
</style>
