<template>
<b-container fluid>
    <b-list-group flush>
        <b-list-group-item class="widget p-0 pb-4 separator">
            <b-container fluid>
                <b-row>
                    <b-col cols=12 class="text-left text-uppercase">
                        <span class="panel-title">Active Configuration</span>
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Host</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        {{host}}
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Port</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        {{port}}
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Connect Route</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        {{connectRoute}}
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Relay Route</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        {{relayRoute}}
                    </b-col>
                </b-row>
                 <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Run Update Route</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        {{runUpdateRoute}}
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Status</div>
                    </b-col>
                    <b-col cols=9 class="text-left">
                        <v-icon v-show="connected" name="check-circle" class="text-success"></v-icon>
                        <v-icon v-show="!connected" name="exclamation-circle" class="text-danger"></v-icon>
                    </b-col>
                </b-row>
            </b-container>
        </b-list-group-item>
        <b-list-group-item class="widget p-0 pt-4">
            <b-container fluid>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Host</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        <b-form-input v-model="hostInput"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Port</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        <b-form-input v-model="portInput"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Connect Route</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        <b-form-input v-model="connectRouteInput"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Relay Route</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        <b-form-input v-model="relayRouteInput"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col cols=3>
                        <div class="text-right">Run Update Route</div>
                    </b-col>
                    <b-col cols=9 class="text-left p-0">
                        <b-form-input v-model="runUpdateRouteInput"></b-form-input>
                    </b-col>
                </b-row>
                <b-row class="mt-4">
                    <b-col>
                        <b-btn class="agent-button m-2"
                            @click="testConfig"
                        >Test 
                            <v-icon v-show="testLoading" name="spinner" spin></v-icon>
                            <v-icon v-show="showTestResult && testResult" name="check-circle" class="text-success"></v-icon>
                            <v-icon v-show="showTestResult && !testResult" name="exclamation-circle" class="text-danger"></v-icon>
                        </b-btn>
                        <b-btn class="agent-button m-2" @click="updateConfig">Confirm</b-btn>
                        <b-btn class="agent-button m-2" @click="agentOverride(true)">Override Agent Action Codes</b-btn>
                        <b-btn class="agent-button m-2" @click="agentOverride(false)">Use Agent Action Codes</b-btn>
                    </b-col>
                </b-row>
            </b-container>
        </b-list-group-item>
    </b-list-group>   
</b-container>
</template>
<script>
import 'vue-awesome/icons/spinner'
import 'vue-awesome/icons/check-circle'
import 'vue-awesome/icons/exclamation-circle'
import axios from 'axios'
import Icon from 'vue-awesome/components/Icon'
export default {
    name: 'AgentConfig',
    components: {
        'v-icon':Icon
    },
    data(){
        return{
            hostInput:"localhost",
            portInput: "8086",
            connectRouteInput: "/",
            relayRouteInput: "/",
            runUpdateRouteInput: "/",
            connected: false,
            host:"",
            port: "",
            connectRoute: "",
            relayRoute: "",
            runUpdateRoute: "",
            testResult: false,
            showTestResult: false,
            testLoading: false
        }
    },
    watch:{
        hostInput: function(){
            this.showTestResult = false
        },
        portInput: function(){
            this.showTestResult = false
        },
        connectRouteInput: function(){
            this.showTestResult = false
        },
        relayRouteInput: function(){
            this.showTestResult = false
        }
    },
    mounted(){
        var instance = this
        axios.get('http://localhost:8085/agent/config')
            .then(function(response){
                instance.host = response.data.host
                instance.port = response.data.port
                instance.connectRoute = response.data['connect.route']
                instance.relayRoute = response.data['relay.route']
                instance.runUpdateRoute = response.data['run.update.route']
                instance.connected = response.data.connected
            })
    },
    methods:{
        updateConfig: function(){
            var instance = this
            var data = {
                port: Number(instance.portInput),
                host: instance.hostInput,
            }
            data['connect.route'] = instance.connectRouteInput
            data['relay.route'] = instance.relayRouteInput
            data['run.update.route'] = instance.runUpdateRouteInput

            axios.post('http://localhost:8085/agent/config', data).then(
                function(response){
                    instance.host = response.data.host
                    instance.port = response.data.port
                    instance.connectRoute = response.data['connect.route']
                    instance.relayRoute = response.data['relay.route']
                    instance.updateRunRoute = response.data['run.update.route']
                    instance.connected = response.data.connected
                }
            ).catch(function(response){
                console.log('Error updating agent config!')
                console.log(response)
            })
        },
        testConfig: function(){
            var instance = this

            //Show loading icon
            instance.testLoading = true

            var data = {
                port: Number(instance.portInput),
                host: instance.hostInput,
            }
            data['connect.route'] = instance.connectRouteInput
            data['relay.route'] = instance.relayRouteInput
            data['run.update.route'] = instance.runUpdateRouteInput
            axios.post('http://localhost:8085/agent/config/test', data).then(
                function(response){
                    console.log(response.data)
                    //Hide loading icon
                    instance.testLoading = false
                    //Show results
                    instance.testResult = response.data.connected
                    instance.showTestResult = true
                }
            ).catch(function(response){
                //Hide loading icon
                instance.testLoading = false
                //Show results
                instance.testResult = false
                instance.showTestResult = true
                console.log('Error testing agent config!')
                console.log(response)
            })
        },
        agentOverride: function(value){
            var instance = this
            axios.get('http://localhost:8085/agent?override=' +  value).then(function(response){
                console.log(response.data)
                instance.$emit('serverState', response.data)
            })
        }
    }
}
</script>
<style scoped>

.separator{
    border-bottom: 5px solid #6c6161 !important;
}

.widget{
    background-color: #3b3535 !important;
    border: none;
}
.panel-title{
    color:#B47F22;
    font-family: 'Open Sans', sans-serif;
    font-size: 16pt;
    font-weight: 600;
}

input{
    background-color: #6c6161;
    color: #f2f2f2;
    border-radius: 0;
    border: none;
}

.agent-button{
    background-color:#6c6161;
    border: 2px solid #B47F22;
    border-radius: 0;
    width: 250px;
}
</style>